.reviews {
  background-color: $bgColor;
  .container {
    padding-top: 104px;
    padding-bottom: 130px;
    position: relative;
  }
  img {
    height: 137px;
    border-radius: 25px;
  }
}
.reviews-star-1 {
  display: none;
  @include tablet {
    position: absolute;
    top: 35px;
    display: block;
  }
  @include desktop-2 {
    left: 0px;
  }
}
.reviews-star-2 {
  position: absolute;
  bottom: 0;
  right: 0;
}
.reviews-title {
  font-family: 'Oranienbaum';
  @include text-base(400, 72px, 0.85);
  text-align: center;
  letter-spacing: -0.015em;
  margin-bottom: 64px;
}
.review-wrap {
  height: 200px;
  display: flex;
  background-color: $lightBrown;
  align-items: flex-start;
  padding: 32px;
  border-radius: 30px;
}
.reviews-about {
  display: flex;
  flex-direction: column;
  margin-left: 32px;
}
.service-type {
  @include text-base(null, 12px, 1.4);
  color: $bgColor;
  max-width: 110px;
  padding: 4px 35px;
  background-color: $lightRed;
  border-radius: 24px;
  margin-bottom: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.another-type {
  background-color: $lightGreen;
}
.name-reviews {
  @include text-base(null, 20px, 1.4);
  color: $lightGreen;
  margin-bottom: 8px;
}
.review-text {
  @include text-base(null, 13px, 1.4);
}
